@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif !important;
  color: black !important;
}

.app {
    padding: 10px;
  }
  
.profile {
    text-align: left;
    margin-bottom: 10px;
  }
.profile-name {
    text-align: center;
    font-size: 33px;
    padding-top: 50px; 
  }
  
.navigation {
    text-align: center;

  }
  
.body {
    max-width: 880px;
    margin-top: 80px;
    margin-left: auto;
    margin-right: auto;
    font-size: 16px;
  }
.profile-image {
    text-align: right;
    shape-outside: circle();
    float: right;
    width: 250px;
    margin: 100px 30px 30px 30px;
    padding: 0px 20px 20px 20px;
    border-radius: 100%; 
  }


.card {
    margin-bottom: 10px;
}
